import { Component } from 'react';
import './Message.scss';

interface IMessageProps {
  error: any;
  message: any;
  className: any;
  hide: any;
}

class Message extends Component<IMessageProps> {
  timer: any[];

  constructor(props: IMessageProps) {
    super(props);

    this.timer = [];
  }

  componentWillReceiveProps(props: IMessageProps) {
    this.setState({ ...this.state, props });

    if (props.error) {
      console.log(props.error);
    }

    if (props.message) {
      if (this.timer[props.message]) {
        clearTimeout(this.timer[props.message]);
        this.timer[props.message] = null;
      }

      this.timer[props.message] = setTimeout(() => {
        this.props.hide();
      }, 5000);
    }
  }

  render() {
    return (
      <div>
        {this.props.message && (
          <div className={`notification ${this.props.className}`}>
            <button className="delete" onClick={() => this.props.hide}></button>
            {this.props.message}
          </div>
        )}
      </div>
    );
  }
}

export default Message;
