import React, { Component } from 'react';
import { Utils } from '../../services/Utils';
import Flip from './Flip';
import { EthereumService } from '../../services/EthereumService';
import land from '../../assets/land.png';
import mine from '../../assets/mine.png';
import tractor from '../../assets/tractor.png';
import world from '../../assets/world.png';
import '../Article.scss';
import './Farm.scss';
import { TokenId } from '../../models/Enums';
import { ICropService } from './Services/CropServiceBase';
import { createCropService } from './Services/CropServiceFactory';
import { Subscription } from 'rxjs';
import { ContractsData, IContractsData } from '../../models/ContractsData';
import { AssetType } from '../../models/AssetConfig';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss';
import collectible from '../../assets/collectible.png';

import { INftDetails } from '../../models/NftDetails';
import { MessagingService } from '../../services/MessagingService';

interface IFarmProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

interface IFarmState {
  allocateLand: any;
  releaseLand: any;
  compostCropAmount: any;
  isReleasing: boolean;
  contractData: IContractsData;
}

type INftDetailsOrIndex = INftDetails | number;

class Farm extends Component<IFarmProps, IFarmState> {
  collectiblesPerPage = 5;
  cropServices?: ICropService[];
  cropService?: ICropService;
  subscription: Subscription;
  carousel?: any;
  swiper?: Swiper;
  collectibles?: INftDetailsOrIndex[];

  constructor(props: IFarmProps) {
    super(props);

    this.state = {
      allocateLand: 0,
      releaseLand: 0,
      compostCropAmount: 0,
      isReleasing: false,
      contractData: new ContractsData()
    };

    this.subscription = new Subscription();

    this.buyLand = this.buyLand.bind(this);
    this.buyCrop = this.buyCrop.bind(this);
    this.approveContract = this.approveContract.bind(this);
    this.handleAllocateLand = this.handleAllocateLand.bind(this);
    this.maxAllocateLand = this.maxAllocateLand.bind(this);
    this.allocateLand = this.allocateLand.bind(this);
    this.releaseLand = this.releaseLand.bind(this);
    this.cancelRelease = this.cancelRelease.bind(this);
    this.confirmRelease = this.confirmRelease.bind(this);
    this.harvestCrop = this.harvestCrop.bind(this);
    this.handleCompostCropAmount = this.handleCompostCropAmount.bind(this);
    this.maxCompostCropAmount = this.maxCompostCropAmount.bind(this);
    this.compostCrop = this.compostCrop.bind(this);
  }

  componentDidMount() {
    this.subscription.add(
      this.props.messagingService.networkEvents.subscribe((event: any) => {
        this.init();
      })
    );

    this.subscription.add(
      this.props.messagingService.events.subscribe((event: any) => {
        this.refresh();
      })
    );

    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  init() {
    if (
      this.props.ethereumservice.networkManager!.network &&
      this.props.ethereumservice.networkManager!.network!.assets.size > 0
    ) {
      this.cropServices = [];

      this.props.ethereumservice.networkManager!.network!.assets.forEach((asset, assetId) => {
        if (asset.assetConstants!.assetType === AssetType.Crop) {
          var cropService = createCropService(this.props.messagingService, this.props.ethereumservice, assetId, asset);
          cropService.update();
          this.cropServices!.push(cropService);
        }
      });

      if (this.cropServices.length > 0) {
        this.cropService = this.cropServices[0];
        this.cropService!.update();
        this.setState({ contractData: new ContractsData() });
        this.refresh();
      } else {
        if (this.props.ethereumservice.networkManager!.network.chainId === 42161) {
        } else {
          this.props.messagingService.errors.next({
            message: `Error: unable to load crop contract`,
            error: 'Unable to load crop contract'
          });
        }
      }
    }
  }

  async refresh() {
    if (!this.props.ethereumservice.walletData.isConnected) {
      return;
    }

    if (!this.cropService) {
      this.init();
    } else {
      this.cropService!.loadBalances().then(() => {
        this.setState({ contractData: this.cropService!.contracts });
      });

      this.collectibles = await this.cropService!.getCollectibles();
      if (this.collectibles.length === 0) {
        this.collectibles = undefined;
      } else {
        // for (var i = this.collectibles.length; i < this.collectiblesPerPage; i++) {
        //   this.collectibles.push(i);
        // }
      }
    }
  }

  buyLand() {
    window.open(
      this.props.ethereumservice.networkManager!.network!.dexBaseUrl! +
        this.props.ethereumservice.networkManager!.network!.assets.get(TokenId.LAND)!.contractAddress,
      '_blank'
    );
  }

  buyCrop() {
    window.open(
      this.props.ethereumservice.networkManager!.network!.dexBaseUrl! +
        this.props.ethereumservice.networkManager!.network!.assets.get(this.cropService!.assetId)!.contractAddress,
      '_blank'
    );
  }

  approveContract() {
    this.cropService!.approveContract();
  }

  // Allocate Land
  handleAllocateLand(e: any) {
    this.setState({ allocateLand: e.target.value });
  }

  maxAllocateLand() {
    this.setState({ allocateLand: this.cropService!.contracts.landBalance });
  }

  allocateLand() {
    this.cropService!.allocateLand(this.state.allocateLand).then(() => {
      this.setState({ allocateLand: 0 });
    });
  }

  // Release Land

  releaseLand() {
    this.setState({ isReleasing: true });
  }

  cancelRelease() {
    this.setState({ isReleasing: false });
  }

  confirmRelease() {
    this.cropService!.releaseLand();
    this.setState({ isReleasing: false });
  }

  // Harvest
  harvestCrop() {
    this.cropService!.harvest();
  }

  // Compost
  handleCompostCropAmount(e: any) {
    this.setState({ compostCropAmount: e.target.value });
  }

  maxCompostCropAmount() {
    this.setState({ compostCropAmount: this.cropService!.contracts.cropBalance });
  }

  compostCrop() {
    this.cropService!.compost(this.state.compostCropAmount).then(() => {
      this.setState({ compostCropAmount: 0 });
    });
  }

  render() {
    return (
      <section id="farm" className="is-vcentered">
        <div className="hero-body">
          <div className="container is-max-widescreen">
            {!this.cropService && this.props.ethereumservice?.networkManager?.network?.chainId === 42161 && (
              <div className="tile is-parent is-center">
                <article className="is-child box has-background-light is-button is-center">
                  <div className="content px-3">
                    <h2 className="mb-0">Coming Soon!</h2>
                  </div>
                </article>
              </div>
            )}
            {!this.cropService && this.props.ethereumservice?.networkManager?.network?.chainId !== 42161 && (
              <div className="tile is-parent is-center">
                <article
                  className="is-child box has-background-light is-button is-center"
                  onClick={() => this.props.ethereumservice.connectWallet()}>
                  <div className="content px-3">
                    <h2 className="mb-0">Connect Wallet</h2>
                  </div>
                </article>
              </div>
            )}
            {this.cropService && (
              <div className="tile is-ancestor">
                <div className="tile is-vertical is-12">
                  <div className="tile">
                    <div className="tile is-parent">
                      <article className="card tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              <li className="is-active">
                                <a>
                                  <img alt="Farm" className="token" src={tractor} />
                                  <span className="tab-title">Farm</span>
                                </a>
                              </li>
                              <li className="disabled tooltip">
                                <span className="tooltip-text">Coming soon</span>
                                <a>
                                  <img alt="Mine" className="token" src={mine} />
                                  <span className="tab-title">Mine</span>
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>

                        <div className="content">{this.renderManageFarm()}</div>
                      </article>
                    </div>
                    <div className="tile is-parent">
                      <article className="card tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              {this.cropServices!.map((cropService, index) => (
                                <React.Fragment key={cropService.assetId}>
                                  {index === 0 && (
                                    <li className="is-active">
                                      <a>
                                        <img
                                          alt="Crop Token"
                                          className="token"
                                          src={cropService.asset.assetConstants!.image}
                                        />
                                        <span className="tab-title">{cropService.assetName}</span>
                                      </a>
                                    </li>
                                  )}
                                  {index > 0 && (
                                    <li className="disabled tooltip">
                                      <span className="tooltip-text">Coming soon</span>
                                      <a>
                                        <img
                                          alt="Wheat Token"
                                          className="token"
                                          src={cropService.asset.assetConstants!.image}
                                        />
                                        <span className="tab-title">{cropService.assetName}</span>
                                      </a>
                                    </li>
                                  )}
                                </React.Fragment>
                              ))}
                            </ul>
                          </div>
                        </div>
                        <div className="content">{this.renderTendCrops()}</div>
                      </article>
                    </div>
                  </div>
                  <div className="tile">
                    <div className="tile is-parent">
                      <article className="card tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              <li className="is-active">
                                <a>
                                  <img alt="Your Statistics" className="token" src={land} />
                                  <span className="tab-title">Your Statistics</span>
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="content">{this.renderFarmStats()}</div>
                      </article>
                    </div>
                    <div className="tile is-parent">
                      <article className="card tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              <li className="is-active">
                                <a>
                                  <img alt="Global Statistics" className="token" src={world} />
                                  <span className="tab-title">Global Statistics</span>
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="content">{this.renderGlobalStats()}</div>
                      </article>
                    </div>
                  </div>
                  {this.collectibles && (
                    <div className="tile is-parent">
                      <article className="card tile is-child box has-background-light p-2">
                        {this.renderCollectibles()}
                      </article>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
      </section>
    );
  }

  renderManageFarm() {
    return (
      <div>
        {!this.cropService!.contracts.isAuthorized && (
          <ul className="action-control">
            <li>
              <button className="button is-danger my-3" onClick={() => this.approveContract()}>
                Grant planning permission
              </button>
            </li>
          </ul>
        )}

        <div className="info-control">
          {/* UNALLOCATED */}
          <h2 className="info">Unallocated :</h2>
          <h2 className="value">
            <span className="pre">{Utils.toFixed(this.cropService!.contracts.landBalance, 2)} LAND</span>
            {this.cropService!.contracts.landBalance > 0 && !this.props.ethereumservice.pricesService!!.prices.landUsd && (
              <span className="post">
                <button className="button is-white is-loading"></button>
              </span>
            )}
            {this.cropService!.contracts.landBalance > 0 && this.props.ethereumservice.pricesService!.prices.landUsd && (
              <span className="post">
                {' '}
                ($
                {Utils.toFixed(
                  this.props.ethereumservice.pricesService!.prices.landUsd * this.cropService!.contracts.landBalance,
                  2
                )}
                )
              </span>
            )}
          </h2>
        </div>

        <div className="info-control">
          {/* FARMLAND */}
          <h2 className="info">Farmland :</h2>
          <h2 className="value">
            <span className="pre">{Utils.toFixed(this.cropService!.contracts.landInFarm, 2)} LAND</span>
            {this.cropService!.contracts.landInFarm > 0 && !this.props.ethereumservice.pricesService!.prices.landUsd && (
              <span className="post">
                <button className="button is-white is-loading"></button>
              </span>
            )}
            {this.cropService!.contracts.landInFarm > 0 && this.props.ethereumservice.pricesService!.prices.landUsd && (
              <span className="post">
                {' '}
                ($
                {Utils.toFixed(
                  this.props.ethereumservice.pricesService!.prices.landUsd * this.cropService!.contracts.landInFarm,
                  2
                )}
                )
              </span>
            )}
          </h2>
        </div>

        {!this.state.isReleasing && (
          <ul className="action-control">
            <li className="action-form">
              <div className="field has-addons">
                <div className="control">
                  <button
                    className="button is-success is-small"
                    onClick={this.maxAllocateLand}
                    disabled={!this.cropService!.contracts.isAuthorized}>
                    Max{' '}
                  </button>
                </div>

                <div className="control input-control">
                  <input
                    className="input is-success is-small"
                    type="text"
                    placeholder="LAND Amount"
                    value={this.state.allocateLand}
                    onChange={this.handleAllocateLand}
                    disabled={!this.cropService!.contracts.isAuthorized}
                  />
                </div>

                <div className="control">
                  <button
                    className="button is-success is-small"
                    onClick={this.allocateLand}
                    disabled={!this.cropService!.contracts.isAuthorized}>
                    Allocate Land{' '}
                  </button>
                </div>
              </div>
            </li>
          </ul>
        )}

        {!this.state.isReleasing && (
          <ul className="action-control mt-5">
            <li>
              <button className="button is-land is-small" onClick={this.buyLand}>
                Buy Land{' '}
              </button>
            </li>

            <li>
              <button
                className="button is-dark is-small"
                onClick={this.releaseLand}
                disabled={!this.cropService!.contracts.isAuthorized}>
                Release Land{' '}
              </button>
            </li>
          </ul>
        )}

        {this.state.isReleasing && (
          <ul className="action-control mt-3">
            <li className="has-text-danger has-text-weight-bold">
              Warning: Releasing land will cause the permanent loss of any growing crop and reset the farm maturity
              boost. Click cancel to harvest crops first.
            </li>
          </ul>
        )}

        {this.state.isReleasing && (
          <ul className="action-control mt-2">
            <li>
              <button className="button is-small" onClick={this.cancelRelease}>
                Cancel{' '}
              </button>
            </li>

            <li>
              <button className="button is-danger is-small" onClick={this.confirmRelease}>
                Confirm{' '}
              </button>
            </li>
          </ul>
        )}
      </div>
    );
  }

  renderTendCrops() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">{this.cropService!.assetName} growing :</h2>
          <h2 className="value">
            <span className="pre">
              {Utils.toFixed(this.cropService!.contracts.cropToHarvest, 8)} {this.cropService!.assetId}
            </span>
            {this.cropService!.contracts.cropToHarvest > 0 &&
              !this.props.ethereumservice.pricesService!.prices.cropUsd.get(this.cropService!.assetId) && (
                <span className="post">
                  <button className="button is-white is-loading"></button>
                </span>
              )}
            {this.cropService!.contracts.cropToHarvest > 0 &&
              this.props.ethereumservice.pricesService!.prices.cropUsd.get(this.cropService!.assetId) && (
                <span className="post">
                  {' '}
                  ($
                  {Utils.toFixed(
                    this.props.ethereumservice.pricesService!.prices.cropUsd.get(this.cropService!.assetId) *
                      this.cropService!.contracts.cropToHarvest,
                    2
                  )}
                  )
                </span>
              )}
          </h2>
        </div>

        <div className="info-control">
          {/* AVAILABLE */}
          <h2 className="info">Available {this.cropService!.assetName} :</h2>
          <h2 className="value">
            <span className="pre">
              {Utils.toFixed(this.cropService!.contracts.cropBalance, 8)} {this.cropService!.assetId}
            </span>
            {this.cropService!.contracts.cropBalance > 0 &&
              !this.props.ethereumservice.pricesService!.prices.cropUsd.get(this.cropService!.assetId) && (
                <span className="post">
                  <button className="button is-white is-loading"></button>
                </span>
              )}
            {this.cropService!.contracts.cropBalance > 0 &&
              this.props.ethereumservice.pricesService!.prices.cropUsd.get(this.cropService!.assetId) && (
                <span className="post">
                  {' '}
                  ($
                  {Utils.toFixed(
                    this.props.ethereumservice.pricesService!.prices.cropUsd.get(this.cropService!.assetId) *
                      this.cropService!.contracts.cropBalance,
                    2
                  )}
                  )
                </span>
              )}
          </h2>
        </div>

        <ul className="action-control">
          <li>
            <button className="button is-warning is-small" onClick={this.buyCrop}>
              Buy {this.cropService!.assetName}{' '}
            </button>
          </li>

          <li>
            <button
              className="button is-dark is-small"
              onClick={this.harvestCrop}
              disabled={!this.cropService!.contracts.isAuthorized}>
              Harvest {this.cropService!.assetName}{' '}
            </button>
          </li>
        </ul>

        <ul className="action-control mt-5">
          <li className="action-form">
            <div className="field has-addons">
              <div className="control">
                <button
                  className="button is-success is-small"
                  onClick={this.maxCompostCropAmount}
                  disabled={!this.cropService!.contracts.isAuthorized}>
                  Max{' '}
                </button>
              </div>

              <div className="control input-control">
                <input
                  className="input is-success is-small"
                  type="text"
                  placeholder={this.cropService!.assetName}
                  value={this.state.compostCropAmount}
                  onChange={this.handleCompostCropAmount}
                  disabled={!this.cropService!.contracts.isAuthorized}
                />
              </div>

              <div className="control">
                <button
                  className="button is-success is-small"
                  onClick={this.compostCrop}
                  disabled={!this.cropService!.contracts.isAuthorized}>
                  Compost {this.cropService!.assetName}
                </button>
              </div>
            </div>
          </li>
        </ul>
      </div>
    );
  }

  renderFarmStats() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Growing season countdown : </h2>
          {this.cropService!.contracts.growingCycleCountDown &&
            typeof this.cropService!.contracts.growingCycleCountDown === 'string' && (
              <div className="countdown">
                <div className="ticker">
                  <div className="digits">
                    <Flip value="COMPLETE" />
                  </div>
                </div>
              </div>
            )}
          {this.cropService!.contracts.growingCycleCountDown &&
            typeof this.cropService!.contracts.growingCycleCountDown !== 'string' && (
              <div className="countdown">
                <div className="ticker">
                  <div className="digits">
                    <Flip value={this.cropService!.contracts.growingCycleCountDown.days} />
                  </div>
                  <div>
                    <div className="units">days</div>
                  </div>
                </div>
                <div className="ticker">
                  <div className="digits">
                    <Flip value={this.cropService!.contracts.growingCycleCountDown.hours} />
                  </div>
                  <div>
                    <div className="units">hours</div>
                  </div>
                </div>
                <div className="ticker">
                  <div className="digits">
                    <Flip value={this.cropService!.contracts.growingCycleCountDown.minutes} />
                  </div>
                  <div>
                    <div className="units">mins</div>
                  </div>
                </div>
              </div>
            )}
        </div>

        <div className="info-control">
          <h2 className="info">Farm maturity boost : </h2>
          <h2 className="value ">x {Utils.toFixed(this.cropService!.contracts.farmMaturityBoost, 4)}</h2>
          <progress
            className="progress is-success"
            value={this.cropService!.contracts.farmMaturityBoost - 1}
            max="2"></progress>
        </div>

        <div className="info-control">
          <h2 className="info">Composting boost : </h2>
          <h2 className="value">x {Utils.toFixed(this.cropService!.contracts.farmCompostBoost, 4)}</h2>
          <progress className="progress" value={this.cropService!.contracts.farmCompostBoost - 1} max="9"></progress>
        </div>

        <div className="info-control">
          <h2 className="info">{this.cropService!.assetName} composted : </h2>
          <h2 className="value">
            <span className="pre">{Utils.toFixed(this.cropService!.contracts.cropComposted, 8)}</span>
            {typeof this.cropService!.contracts.burnRequiredTo10x == 'number' &&
              typeof this.cropService!.contracts.maxTo10x == 'number' && (
                <span className="post">({Utils.toFixed(this.cropService!.contracts.maxTo10x, 2)} for x 10)</span>
              )}
          </h2>
          {typeof this.cropService!.contracts.burnRequiredTo10x == 'number' && (
            <progress
              className="progress is-warning"
              value={this.cropService!.contracts.cropComposted}
              max={this.cropService!.contracts.maxTo10x}></progress>
          )}
          {typeof this.cropService!.contracts.burnRequiredTo10x != 'number' && (
            <progress className="progress is-error" value={1} max={1}></progress>
          )}
        </div>
      </div>
    );
  }

  renderGlobalStats() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Total Farms : </h2>
          <h2 className="value">{Utils.toFixed(this.cropService!.contracts.globalNumberOfFarms, 0)}</h2>
        </div>
        <div className="info-control">
          <h2 className="info">Total Land in Farms : </h2>
          <h2 className="value">{Utils.toFixed(this.cropService!.contracts.globalLandInFarm, 2)} LAND</h2>
        </div>
        <div className="info-control">
          <h2 className="info">Average Size of Farms : </h2>
          <h2 className="value">{Utils.toFixed(this.cropService!.contracts.globalAverageLandInFarms, 0)} LAND</h2>
        </div>
        <div className="info-control">
          <h2 className="info">Total {this.cropService!.assetName} composted : </h2>
          <h2 className="value">
            {Utils.toFixed(this.cropService!.contracts.globalComposted, 2)} {this.cropService!.assetId}
          </h2>
        </div>
        <div className="info-control">
          <h2 className="info">Average Amount of {this.cropService!.assetName} composted : </h2>
          <h2 className="value">
            {Utils.toFixed(this.cropService!.contracts.globalAverageComposted, 2)} {this.cropService!.assetId}
          </h2>
        </div>
      </div>
    );
  }

  renderCollectibles() {
    if (this.collectibles) {
      return (
        <section className="noselect tooltip">
          <div className="container">
            <Swiper
              grabCursor={true}
              preventClicks={false}
              preventClicksPropagation={false}
              spaceBetween={20}
              slidesPerView={this.collectiblesPerPage}>
              {this.collectibles!.map(c => (
                <SwiperSlide key={typeof c === 'number' ? c : c.index}>
                  <CollectibleCard collectible={c} cropService={this.cropService!}></CollectibleCard>
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        </section>
      );
    } else {
      return <div></div>;
    }
  }
}

interface ICollectibleCardProps {
  collectible: INftDetailsOrIndex;
  cropService: ICropService;
}

interface ICollectibleCardState {
  isFlipped: boolean;
  isRemoving: boolean;
}

class CollectibleCard extends Component<ICollectibleCardProps, ICollectibleCardState> {
  constructor(props: any) {
    super(props);

    this.state = {
      isFlipped: false,
      isRemoving: false
    };

    this.toggleFlipped = this.toggleFlipped.bind(this);
    this.toggleRemove = this.toggleRemove.bind(this);
    this.remove = this.remove.bind(this);
  }

  toggleFlipped(e: any) {
    e.preventDefault();
    this.setState({ isFlipped: !this.state.isFlipped });
  }

  toggleRemove(e: any) {
    e.preventDefault();
    this.setState({ isRemoving: !this.state.isRemoving });
  }

  remove(e: any) {
    e.preventDefault();
    this.props.cropService.releaseCollectible(this.props.collectible as INftDetails);
    this.setState({ isRemoving: !this.state.isRemoving });
  }

  render() {
    if (typeof this.props.collectible !== 'number') {
      if (this.state.isRemoving) {
        return (
          <div className="tile is-parent is-center p-1">
            <article className="media is-child box has-background-light">
              <figure className="media-left">
                <p className="image is-128x128">
                  <img src={this.props.collectible!.image} alt="" />
                </p>
              </figure>
              <div className="media-content">
                <div className="content">
                  <p>
                    <strong>Remove {this.props.collectible!.name}?</strong>
                  </p>
                </div>
                <nav className="level mt-4 is-justify-content-center">
                  <button className="button is-small" onClick={this.toggleRemove}>
                    Cancel
                  </button>
                  <button className="button is-danger is-small" onClick={this.remove}>
                    Remove
                  </button>
                </nav>
              </div>
            </article>
          </div>
        );
      } else {
        return (
          <div className="tile is-parent is-center p-1">
            <article className="media is-child box has-background-light">
              <figure className="media-left">
                <p className="image is-128x128">
                  <img src={this.props.collectible!.image} alt="" />
                </p>
              </figure>
              <div className="media-content">
                <div className="content">
                  <p>
                    <strong>{this.props.collectible!.name}</strong>
                  </p>
                </div>
                <nav className="level mt-4 is-justify-content-center">
                  <button className="button is-small is-dark">More Info</button>
                </nav>
              </div>
              <div className="media-right">
                <span className="tag is-delete" onClick={this.toggleRemove}></span>
              </div>
            </article>
          </div>
        );
      }
    } else {
      return (
        <div className="tile is-parent is-center p-1">
          {/* <article className="media is-child box has-background-light">
            <figure className="media-left">
              <p className="image is-128x128">
                <img src={collectible} alt="" />
              </p>
            </figure>
          </article> */}
        </div>
      );
    }
  }
}

export default Farm;
