import { Component } from 'react';
import { EthereumService } from '../services/EthereumService';
import Content from './Content';
import NavBar from './NavBar';
import Message from './Message';
import { Subscription } from 'rxjs';
import { IWalletData, WalletData } from '../models/WalletData';
import { MessagingService } from '../services/MessagingService';

interface IAppProps {}

interface IAppState {
  approvedTerms: boolean;
  initError: boolean;
  activeItem: string;
  message: string;
  messagePending: string;
  error: string;
  walletData: IWalletData;
}

class App extends Component<IAppProps, IAppState> {
  subscription: Subscription;
  messagingService: MessagingService;
  ethereumService: EthereumService;

  constructor(props: IAppProps) {
    super(props);

    this.state = {
      approvedTerms: localStorage.getItem('approvedTerms') === 'true',
      initError: false,
      activeItem: '',
      message: '',
      messagePending: '',
      error: '',
      walletData: new WalletData()
    };

    this.subscription = new Subscription();
    this.messagingService = new MessagingService();
    this.ethereumService = new EthereumService(this.messagingService);
    this.selectActiveItem = this.selectActiveItem.bind(this);
    this.showMessage = this.showMessage.bind(this);
    this.hideMessage = this.hideMessage.bind(this);
    this.confirmTerms = this.confirmTerms.bind(this);
  }

  componentDidMount() {
    var eventSubscription = this.messagingService!.events.subscribe(
      event => {
        if (event) {
          this.showMessage(event);
        }

        this.setState({ walletData: this.ethereumService.walletData });
      },
      error => this.showMessage({ message: 'Unexpected error.', error: error })
    );

    this.subscription.add(eventSubscription);

    var errorSubscription = this.messagingService!.errors.subscribe(
      event => this.showMessage(event),
      error => this.showMessage({ message: 'Unexpected error.', error: error })
    );

    this.subscription.add(errorSubscription);
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  confirmTerms() {
    this.setState({
      approvedTerms: true
    });

    localStorage.setItem('approvedTerms', 'true');
  }

  showMessage(event: any) {
    this.setState({
      message: event.message,
      messagePending: event.pending,
      error: event.error
    });
  }

  hideMessage() {
    this.setState({
      message: ''
    });
  }

  selectActiveItem(itemId: string) {
    this.setState({ activeItem: itemId });
  }

  renderTerms() {
    return (
      <div className={`${this.state.approvedTerms ? '' : 'is-active'}` + 'modal'}>
        <div className="modal-background"></div>
        <div className="modal-card">
          <header className="modal-card-head">
            <p className="modal-card-title">Notice</p>
          </header>
          <section className="modal-card-body">
            By continuing to use the Farmland site and any associated smart contracts you agree to the following:
            <ul className="mt-2">
              <li className="mt-4">
                1. Farmland is currently in beta and any action you take on the site or interacting with the smart
                contracts is strictly at your own risk.
              </li>
              <li className="mt-4">
                2. You will make use of the Farmland site and associated smart contracts in accordance with all domestic
                and international laws.
              </li>
            </ul>
          </section>
          <footer className="modal-card-foot is-justify-content-center">
            <button className="button is-success" onClick={this.confirmTerms}>
              I Agree
            </button>
          </footer>
        </div>
      </div>
    );
  }

  render() {
    if (this.state.initError) {
      return (
        <div id="app">
          <section className="hero is-fullheight-with-navbar">
            <div className="hero-body">
              <div className="container">
                <h1 className="title">No Wallet</h1>
                <h2 className="subtitle">
                  <a href="https://metamask.io">Metamask</a> or equivalent required to access this page.
                </h2>
              </div>
            </div>
          </section>
        </div>
      );
    } else {
      return (
        <div id="app">
          <NavBar
            ethereumservice={this.ethereumService}
            activeItem={this.state.activeItem}
            // onNavigated={this.selectActiveItem}
          />

          <Message
            message={this.state.message}
            error=""
            hide={this.hideMessage}
            className={`${this.state.error ? 'is-danger' : this.state.messagePending ? 'is-warning' : 'is-success'}`}
          />

          <div id="main" className="hero is-fullheight-with-navbar">
            <div className="hero-head"></div>
            {this.renderTerms()}

            {this.state.approvedTerms && (
              <Content
                messagingService={this.messagingService}
                ethereumservice={this.ethereumService}
                activeItem={this.state.activeItem}
                // onNavigated={this.selectActiveItem}
              />
            )}

            <div className="hero-foot"></div>
          </div>
        </div>
      );
    }
  }
}

export default App;
