import React, { Component } from 'react';
import { Subscription } from 'rxjs';
import { LiquidityAssetType } from '../../../models/Enums';
import { EthereumService } from '../../../services/EthereumService';
import { Utils } from '../../../services/Utils';
import { ILiquidityAssetService } from '../Services/Interfaces';
import { StakingAssetService } from '../Services/StakingAssetService';
import { LpLandAssetService } from '../Services/LpLandAssetService';
import { MessagingService } from '../../../services/MessagingService';
import '../../Article.scss';
import '../Estate.scss';
import './Pools.scss';
import opportunities from '../../../assets/opportunities.png';

interface IOpportunitiesProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

enum Action {
  None = 1,
  Approve,
  Add,
  Remove
}

interface IOpportunitiesState {
  liquidityAssetService?: ILiquidityAssetService | null;
  amount: string | number;
  balance: string | number;
  action: Action;
  liquidityAssetServices: ILiquidityAssetService[];
  showDetail: boolean;
}

class Opportunities extends Component<IOpportunitiesProps, IOpportunitiesState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;

  constructor(props: any) {
    super(props);

    this.state = {
      liquidityAssetService: null,
      amount: 0,
      balance: 0,
      action: Action.None,
      liquidityAssetServices: [],
      showDetail: false
    };

    this.addLiquidity = this.addLiquidity.bind(this);
    this.removeLiquidity = this.removeLiquidity.bind(this);
    this.claimReward = this.claimReward.bind(this);
    this.clearAction = this.clearAction.bind(this);
    this.setAmount = this.setAmount.bind(this);
    this.setPercentAmount = this.setPercentAmount.bind(this);
    this.approveSpend = this.approveSpend.bind(this);
    this.confirmAdd = this.confirmAdd.bind(this);
    this.confirmRemove = this.confirmRemove.bind(this);
    this.confirmExit = this.confirmExit.bind(this);

    this.subscription = new Subscription();
  }

  componentDidMount() {
    var eventSubscription = this.props.messagingService.events.subscribe(event => {
      this.refresh();
    });

    this.subscription.add(eventSubscription);

    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  refresh() {
    if (
      this.props.ethereumservice.walletData.walletAddress &&
      this.props.ethereumservice.networkManager?.network?.chainId
    ) {
      if (
        this.props.ethereumservice.walletData.walletAddress !== this.walletAddress ||
        this.props.ethereumservice.networkManager!.network!?.chainId !== this.chainId
      ) {
        this.walletAddress = this.props.ethereumservice.walletData.walletAddress;
        this.chainId = this.props.ethereumservice.networkManager!.network!.chainId;
        this.init();
      } else {
        this.update();
      }
    }
  }

  init() {
    if (
      this.props.ethereumservice.networkManager?.network &&
      this.props.ethereumservice.networkManager?.network?.liquidityConfigs.length > 0
    ) {
      var liquidityUpdatePromises = this.props.ethereumservice.networkManager!.network!.liquidityConfigs.map(lc => {
        switch (lc.liquidityType) {
          case LiquidityAssetType.Staking:
            var liquidityAssetService = new StakingAssetService(
              lc,
              this.props.ethereumservice,
              this.props.messagingService
            ) as ILiquidityAssetService;
            break;
          case LiquidityAssetType.wLand:
            liquidityAssetService = new LpLandAssetService(
              lc,
              this.props.ethereumservice,
              this.props.messagingService
            ) as ILiquidityAssetService;
            break;
          default:
            throw new Error('Unknown LiquidityAssetType');
        }

        return liquidityAssetService.update().then(() => {
          return liquidityAssetService;
        });
      });

      return Promise.all(liquidityUpdatePromises).then(liquidityAssetServices => {
        this.setState({ liquidityAssetServices: liquidityAssetServices });
      });
    }
  }

  update() {
    this.state.liquidityAssetServices.forEach(liquidityAsset => {
      liquidityAsset.update();
    });

    this.setState({ liquidityAssetServices: this.state.liquidityAssetServices });
  }

  addLiquidity(liquidityAssetService: ILiquidityAssetService) {
    this.setState({
      action: Action.Add,
      liquidityAssetService: liquidityAssetService,
      amount: 0,
      balance: liquidityAssetService.lptBalance
    });
  }

  removeLiquidity(liquidityAssetService: ILiquidityAssetService) {
    this.setState({
      action: Action.Remove,
      liquidityAssetService: liquidityAssetService,
      amount: 0,
      balance: liquidityAssetService.liquidityBalance
    });
  }

  claimReward(liquidityAssetService: ILiquidityAssetService) {
    liquidityAssetService!.claim();
  }

  clearAction() {
    this.setState({ action: Action.None, liquidityAssetService: null, amount: 0 });
  }

  setAmount(e: any) {
    this.setState({ amount: e.target.value });
  }

  setPercentAmount(percentage: number) {
    var addAmount = Utils.formatUnits(Utils.parseUnits(this.state.balance).mul(percentage).div(100));
    this.setState({ amount: addAmount });
  }

  approveSpend(liquidityAssetService: ILiquidityAssetService) {
    liquidityAssetService!.approve();
    this.clearAction();
  }

  confirmAdd() {
    this.state.liquidityAssetService!.add(this.state.amount);
    this.clearAction();
  }

  confirmRemove() {
    this.state.liquidityAssetService!.remove(this.state.amount);
    this.clearAction();
  }

  confirmExit() {
    this.state.liquidityAssetService!.exit(this.state.amount);
    this.clearAction();
  }

  render() {
    return (
      <section id="wallet" className="is-vcentered">
        <div className="hero-body">
          <div className="container is-max-widescreen">
            {!this.props.ethereumservice.walletData.isConnected && (
              <div className="tile is-parent is-center">
                <article
                  className="is-child box has-background-light is-button is-center"
                  onClick={() => this.props.ethereumservice.connectWallet()}>
                  <div className="content px-3">
                    <h2 className="mb-0">Connect Wallet</h2>
                  </div>
                </article>
              </div>
            )}
            {this.props.ethereumservice.walletData.isConnected && (
              <div className="tile is-ancestor">
                <div className="tile is-vertical is-12">
                  <div className="tile is-parent">
                    <article className="card tile is-child box has-background-light">
                      <div className="title">
                        <div className="tabs is-fullwidth is-medium is-boxed">
                          <ul>
                            <li className="is-active">
                              <a>
                                <img alt="Pools" className="token" src={opportunities} />
                                <span className="tab-title">Pools</span>
                              </a>
                            </li>
                          </ul>
                        </div>
                      </div>
                      {!this.state.liquidityAssetService && <div className="content">{this.renderOpportunities()}</div>}
                      {this.state.liquidityAssetService && this.state.action === Action.Add && (
                        <div className="narrow-content">{this.renderAddLiquidity()}</div>
                      )}
                      {this.state.liquidityAssetService && this.state.action === Action.Remove && (
                        <div className="narrow-content">{this.renderRemoveLiquidity()}</div>
                      )}
                    </article>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </section>
    );
  }

  renderOpportunities() {
    return (
      <div>
        <div className="table-control">
          <table className="table is-hoverable is-fullwidth">
            <thead>
              <tr>
                <th></th>
                <th>Staking</th>
                <th>Rewards</th>
                {/* <th>Global Staked</th> */}
                {/* <th>ROI</th> */}
                <th></th>
              </tr>
            </thead>
            <tbody>
              {/* TODO add filter and message if there aren't any opps
              .filter(o => o.lptBalance > 0) */}
              {this.state.liquidityAssetServices.map(la => (
                <OpportunityRow
                  key={la.config.poolContractAddress}
                  onApproveClick={this.approveSpend}
                  onAddClick={this.addLiquidity}
                  onRemoveClick={this.removeLiquidity}
                  onClaimClick={this.claimReward}
                  liquidityAssetService={la}></OpportunityRow>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  }

  renderAddLiquidity() {
    return (
      <div className="mb-3">
        {this.state.liquidityAssetService!.lptBalance <= 0 && (
          <ul className="action-control mt-5 is-vertical">
            <li className="is-center  is-fullwidth m-0">
              <h2 className="info">Nothing to {this.state.liquidityAssetService!.actionDisplayName}</h2>
            </li>

            <li>
              <button className="button is-small" onClick={this.clearAction}>
                OK{' '}
              </button>
            </li>
          </ul>
        )}

        {this.state.liquidityAssetService!.lptBalance > 0 && (
          <React.Fragment>
            <div className="info-control">
              <h2 className="info is-center">{this.state.liquidityAssetService!.actionDisplayName} :</h2>
              <h2 className="value is-center">
                <span className="pre">
                  {Utils.toFixed(this.state.liquidityAssetService!.lptBalance, 8)}{' '}
                  {this.state.liquidityAssetService!.config.lpToken}
                </span>
              </h2>
            </div>

            {this.renderAddAmount()}

            <ul className="action-control mt-5">
              <li>
                <button className="button is-small" onClick={this.clearAction}>
                  Cancel
                </button>
              </li>

              <li>
                <button
                  className="button is-success is-small"
                  disabled={this.state.amount <= 0}
                  onClick={this.confirmAdd}>
                  {this.state.liquidityAssetService!.actionDisplayName}
                </button>
              </li>
            </ul>
          </React.Fragment>
        )}
      </div>
    );
  }

  renderRemoveLiquidity() {
    return (
      <div className="mb-3">
        {this.state.liquidityAssetService!.liquidityBalance <= 0 && (
          <ul className="action-control mt-5 is-vertical">
            <li className="is-center  is-fullwidth m-0">
              <h2 className="info">Nothing to {this.state.liquidityAssetService!.undoActionDisplayName}</h2>
            </li>

            <li>
              <button className="button is-small" onClick={this.clearAction}>
                OK{' '}
              </button>
            </li>
          </ul>
        )}

        {this.state.liquidityAssetService!.liquidityBalance > 0 && (
          <React.Fragment>
            <div className="info-control">
              <h2 className="info is-center">{this.state.liquidityAssetService!.undoActionDisplayName} :</h2>
              <h2 className="value is-center">
                <span className="pre">
                  {Utils.toFixed(this.state.liquidityAssetService!.liquidityBalance, 8)}{' '}
                  {this.state.liquidityAssetService!.config.lpToken}
                </span>
              </h2>
              <h2 className="info mt-3 has-text-danger">Warning: Unstaking will forfeit the following:</h2>
              <h2 className="info is-center">Upcoming Reward :</h2>
              <h2 className="value is-center">
                <span className="pre">{Utils.toFixed(this.state.liquidityAssetService!.upcomingReward, 8)} </span>
              </h2>
            </div>

            {this.renderRemoveAmount()}

            <ul className="action-control mt-5">
              <li>
                <button className="button is-small" onClick={this.clearAction}>
                  Cancel
                </button>
              </li>

              <li>
                <button
                  className="button is-success is-small"
                  disabled={this.state.amount <= 0}
                  onClick={this.confirmRemove}>
                  {this.state.liquidityAssetService!.undoActionDisplayName}
                </button>
              </li>

              {this.state.liquidityAssetService!.currentReward > 0 && (
                <li>
                  <button
                    className="button is-success is-small"
                    disabled={this.state.amount <= 0}
                    onClick={this.confirmExit}>
                    {this.state.liquidityAssetService!.undoActionDisplayName} and Claim Reward
                  </button>
                </li>
              )}
            </ul>
          </React.Fragment>
        )}
      </div>
    );
  }

  renderApproveAmount() {
    return (
      <ul className="action-control mt-5">
        <li className="action-form">
          <div className="field has-addons">
            {this.renderPercentButtons()}

            <div className="control input-control">
              <input
                className="input is-success is-small"
                type="text"
                placeholder="BPT Amount"
                value={this.state.amount}
                onChange={this.setAmount}
                disabled={this.state.liquidityAssetService!.lptBalance <= 0}
              />
            </div>
          </div>
        </li>
      </ul>
    );
  }

  renderAddAmount() {
    return (
      <ul className="action-control mt-5 is-vertical">
        <li className="is-center  is-fullwidth m-0">{this.renderPercentButtons()}</li>

        <li className="is-center is-fullwidth m-0">
          <div className="control input-control">
            <input
              className="input is-primary is-small"
              type="text"
              placeholder="BPT Amount"
              value={this.state.amount}
              onChange={this.setAmount}
              disabled={this.state.liquidityAssetService!.approvedLptBalance <= 0}
            />
          </div>
        </li>
      </ul>
    );
  }

  renderRemoveAmount() {
    return (
      <ul className="action-control mt-5 is-vertical">
        <li className="is-center  is-fullwidth m-0">{this.renderPercentButtons()}</li>

        <li className="is-center is-fullwidth m-0">
          <div className="control input-control">
            <input
              className="input is-primary is-small"
              type="text"
              placeholder="BPT Amount"
              value={this.state.amount}
              onChange={this.setAmount}
              disabled={this.state.liquidityAssetService!.liquidityBalance <= 0}
            />
          </div>
        </li>
      </ul>
    );
  }

  renderPercentButtons() {
    return (
      <div className="buttons has-addons">
        <button className="button is-white is-small" onClick={() => this.setPercentAmount(25)}>
          25 %
        </button>

        <button className="button is-gray is-small" onClick={() => this.setPercentAmount(50)}>
          50 %
        </button>

        <button className="button is-darkgray is-small" onClick={() => this.setPercentAmount(75)}>
          75 %
        </button>

        <button className="button is-darkergray is-small" onClick={() => this.setPercentAmount(100)}>
          100 %
        </button>
      </div>
    );
  }
}

interface IOpportunityRowProps {
  liquidityAssetService?: ILiquidityAssetService;
  onApproveClick: (liquidityAssetService: ILiquidityAssetService) => void;
  onAddClick: (liquidityAssetService: ILiquidityAssetService) => void;
  onRemoveClick: (liquidityAssetService: ILiquidityAssetService) => void;
  onClaimClick: (liquidityAssetService: ILiquidityAssetService) => void;
}

interface IOpportunityRowState {
  dropdownActiveClass: string;
}

class OpportunityRow extends Component<IOpportunityRowProps, IOpportunityRowState> {
  constructor(props: IOpportunityRowProps) {
    super(props);

    this.state = {
      dropdownActiveClass: ''
    };

    this.dropdownClick = this.dropdownClick.bind(this);
    this.dropdownItemClick = this.dropdownItemClick.bind(this);
  }

  approve = () => {
    this.props.onApproveClick(this.props.liquidityAssetService!);
  };
  add = () => {
    this.props.onAddClick(this.props.liquidityAssetService!);
  };
  remove = () => {
    this.props.onRemoveClick(this.props.liquidityAssetService!);
  };
  claim = () => {
    this.props.onClaimClick(this.props.liquidityAssetService!);
  };

  dropdownClick(e: any) {
    this.setState({
      dropdownActiveClass: this.state.dropdownActiveClass === '' ? 'is-active' : ''
    });
    e.stopPropagation();
  }

  dropdownItemClick(e: any) {
    this.setState({
      dropdownActiveClass: ''
    });
  }

  render() {
    return (
      <tr>
        <td>
          <div className="flex">
            <div className="tLogo">
              <div className="tLogo_group">
                <img
                  alt={this.props.liquidityAssetService!.config.token1}
                  className="token"
                  src={this.props.liquidityAssetService!.config.token1Icon}
                />
                <img
                  alt={this.props.liquidityAssetService!.config.token2}
                  className="token"
                  src={this.props.liquidityAssetService!.config.token2Icon}
                />
              </div>
              <img
                alt={this.props.liquidityAssetService!.config.pool}
                className="tExtra"
                src={this.props.liquidityAssetService!.config.poolIcon}
              />
            </div>
            <div>
              <div className="cell_PrimaryText">
                {this.props.liquidityAssetService!.config.token1} / {this.props.liquidityAssetService!.config.token2}
              </div>
              <div className="cell_SecondaryText">{this.props.liquidityAssetService!.config.pool}</div>
            </div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_TitleText">Staking</div>
            <div className="cell_SecondaryText">
              Available {Utils.toFixed(this.props.liquidityAssetService!.lptBalance, 8)}{' '}
              {this.props.liquidityAssetService!.config.lpToken}
            </div>
            <div className="cell_SecondaryText">
              Staked {Utils.toFixed(this.props.liquidityAssetService!.liquidityBalance, 8)}{' '}
              {this.props.liquidityAssetService!.config.lpToken}
            </div>
          </div>
        </td>
        <td>
          <div>
            <div className="cell_TitleText">Rewards</div>
            {this.props.liquidityAssetService!.currentReward > 0 && (
              <div className="cell_SecondaryText has-text-danger has-text-weight-semibold">
                Claimable {Utils.toFixed(this.props.liquidityAssetService!.currentReward, 8)}{' '}
                {this.props.liquidityAssetService!.config.rewardToken}
              </div>
            )}
            {this.props.liquidityAssetService!.currentReward <= 0 && (
              <div className="cell_SecondaryText">
                Claimable {Utils.toFixed(this.props.liquidityAssetService!.currentReward, 8)}{' '}
                {this.props.liquidityAssetService!.config.rewardToken}
              </div>
            )}
            <div className="cell_SecondaryText">
              Upcoming {Utils.toFixed(this.props.liquidityAssetService!.upcomingReward, 8)}{' '}
              {this.props.liquidityAssetService!.config.rewardToken}
            </div>
          </div>
        </td>
        {/* <td>
          <div>
            <div className="cell_TitleText">Global Staked</div>
            <div className="cell_PrimaryText">
              {Utils.toFixed(this.props.liquidityAssetService!.liquidityBalance, 8)}{' '}
              {this.props.liquidityAssetService!.config.lpToken}
            </div>
          </div>
        </td> */}
        {/* <td>
          <div>
            <div className="cell_PrimaryText">{Utils.toFixed(this.props.liquidityAssetService!.roi, 2)}% (1y)</div>
            <div className="cell_SecondaryText">
              {Utils.toFixed(this.props.liquidityAssetService!.roi / 52, 2)}% (1w)
            </div>
          </div>
        </td> */}
        <td>
          <div className="has-text-centered">
            <div>{this.renderActionDropDown()}</div>
          </div>
        </td>
      </tr>
    );
  }

  renderActionDropDown() {
    if (this.props.liquidityAssetService!.lptBalance <= 0 && this.props.liquidityAssetService!.liquidityBalance <= 0) {
      return (
        <a
          className="button is-success"
          target="_blank"
          rel="noopener noreferrer"
          href={this.props.liquidityAssetService!.config.poolUrl}>
          Add
        </a>
      );
    } else {
      return (
        <div className={`action-dropdown dropdown ${this.state.dropdownActiveClass}`}>
          <div className="dropdown-trigger">
            <button
              className="button is-success"
              aria-haspopup="true"
              aria-controls="dropdown-menu"
              onClick={this.dropdownClick}>
              <span>Action</span>
              <span className="icon is-small">
                <i className="fas fa-angle-down" aria-hidden="true"></i>
              </span>
            </button>
          </div>
          <div className="dropdown-menu" id="dropdown-menu" role="menu" onClick={this.dropdownItemClick}>
            <div className="dropdown-content">
              {this.props.liquidityAssetService!.unapprovedLptBalance > 0 && (
                <button className="button is-link is-transparent" onClick={this.approve}>
                  <span>Approve</span>
                </button>
              )}
              {this.props.liquidityAssetService!.approvedLptBalance > 0 &&
                this.props.liquidityAssetService!.lptBalance > 0 && (
                  <button className="button is-link is-transparent" onClick={this.add}>
                    <span>{this.props.liquidityAssetService!.actionDisplayName}</span>
                  </button>
                )}
              {this.props.liquidityAssetService!.liquidityBalance > 0 && (
                <button className="button is-link is-transparent" onClick={this.remove}>
                  <span>{this.props.liquidityAssetService!.undoActionDisplayName}</span>
                </button>
              )}
              {this.props.liquidityAssetService!.currentReward > 0 && (
                <button className="button is-link is-transparent" onClick={this.claim}>
                  <span>Claim Reward</span>
                </button>
              )}
              <a
                className="button is-success"
                target="_blank"
                rel="noopener noreferrer"
                href={this.props.liquidityAssetService!.config.poolUrl}>
                Add
              </a>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default Opportunities;
