import { Component } from 'react';
import Farm from './Farm/Farm';
import Landing from './Landing';
import Docs from './Docs';
import ErrorBoundary from './ErrorBoundary';
import { EthereumService } from '../services/EthereumService';

//@ts-ignore
import { Switch, Route, Redirect } from 'react-router-dom';
import { MessagingService } from '../services/MessagingService';
import Wallet from './Estate/Wallet/Wallet';
import Collectibles from './Estate/Collectibles/Collectibles';
import Pools from './Estate/Pools/Pools';

interface IContentProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
  activeItem: any;
}

class Content extends Component<IContentProps> {
  render() {
    return (
      <div id="content">
        <Switch>
          <Route exact path="/">
            <Landing />
          </Route>

          <Route path="/farm">
            <ErrorBoundary>
              <Farm messagingService={this.props.messagingService} ethereumservice={this.props.ethereumservice} />
            </ErrorBoundary>
          </Route>

          <Route path="/wallet">
            <ErrorBoundary>
              <Wallet messagingService={this.props.messagingService} ethereumservice={this.props.ethereumservice} />
            </ErrorBoundary>
          </Route>

          <Route path="/collectibles">
            <ErrorBoundary>
              <Collectibles
                messagingService={this.props.messagingService}
                ethereumservice={this.props.ethereumservice}
              />
            </ErrorBoundary>
          </Route>

          <Route path="/pools">
            <ErrorBoundary>
              <Pools messagingService={this.props.messagingService} ethereumservice={this.props.ethereumservice} />
            </ErrorBoundary>
          </Route>

          <Route path="/docs">
            <Docs ethereumservice={this.props.ethereumservice} />
          </Route>

          <Redirect to="/" />
        </Switch>
      </div>
    );
  }
}

export default Content;
