import { IAssetConfig } from './AssetConfig';

export interface INftAttribute {
  id: number;
  display_type: string;
  trait_type: string;
  value: any;
}

export interface INftDetails {
  symbol: string;
  index: number;
  attributes: INftAttribute[];
  description: string;
  image: string;
  name: string;
}

export async function loadNftDetailsFromUri(tokenUri: string, index: number): Promise<INftDetails> {
  const tokenResponse = await fetch(tokenUri.replace('ipfs://', 'https://gateway.pinata.cloud/ipfs/'));
  var nft = (await tokenResponse.json()) as INftDetails;

  var toRemove = ['Background', 'Body', 'Head', 'Eyes', 'Nose', 'Mouth', 'Shirt', 'Extra'];
  nft.attributes = nft.attributes.filter(a => !toRemove.includes(a.trait_type));

  var dateAttribute = nft.attributes.find(a => a.trait_type === 'Date of Birth');
  dateAttribute!.value = new Date(dateAttribute!.value * 1000).toDateString();

  nft.image = nft.image.replace('ipfs://', 'https://gateway.pinata.cloud/ipfs/');
  nft.index = index;
  return nft;
}
